.card {
  padding: 17px 12px;
  background: #FFFFFF;
  box-shadow: 0px 4px 18px rgba(88, 126, 236, 0.18);
  border-radius: 8px;
  transition: all .3s ease;
}

.card:hover {
  transform: translateY(-10px);
}

@media screen and (min-width: 953px) {
  .card {
    padding: 37px 25px;
  }

}