.intro__video {
  margin: 30px 0 0;
  display: inline-flex;
  flex-wrap: wrap;
  align-items: center;
  text-decoration: none;
  color: #B0B0B0;
}

.intro__video:hover {
  text-decoration: underline;
}

@media screen and (min-width: 867.98px) {
  .intro__video {
    margin-top: 60px;
  }
}