  content: "";
  background: #000;
  height: 2px;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
  position: absolute;
  top: 50%;
  width: 100%;
  right: 0;
  background: #000;
  height: 2px;
  transform: rotate(45deg);
}

.header__close::after {
  content: "";
