  width: 66px;
  height: 66px;
  background: #EC5863;
  border-radius: 16px;
  -webkit-transform: rotate(-30deg);
          transform: rotate(-30deg);
  position: absolute;
  top: 0px;
  right: 10px;
  z-index: 0;
}

@media screen and (min-width: 678px) {
  .healthcare__boxes::before {
    bottom: 0px;
  }

  .healthcare__boxes::after {
    right: 5px;
  }
}
.healthcare__cards {
  max-width: 1057px;
  margin: 0;
  padding: 20px 15px;
  list-style-type: none;
  display: grid;
  grid-template-columns: repeat(3, 99%);
  grid-auto-flow: column;
  grid-gap: 30px;
  overflow-y: visible;
  overflow-x: auto;
  scrollbar-width: thin;
  scrollbar-color: #EC5863 #cccdce80;
  position: relative;
  z-index: 2;
  overflow-y: hidden;
}

.healthcare__cards::-webkit-scrollbar {
  height: 3px;
}

.healthcare__cards::-webkit-scrollbar-track {
  background: #cccdce80;
  border-radius: 6px;
}

.healthcare__cards::-webkit-scrollbar-thumb {
  background: #EC5863;
  border-radius: 6px;
}


@media screen and (min-width: 480.98px) {
  .healthcare__cards {
    grid-template-columns: repeat(3, 46%);
  }
}

@media screen and (min-width: 678px) {
  .healthcare__cards {
    grid-template-columns: repeat(3, calc((100% - 65px) / 3));
  }

  .healthcare__cards::before {
    content: '';
    width: 97px;
    height: 97px;
    background: #EC5863;
    border-radius: 30px;
    -webkit-transform: rotate(105deg);
            transform: rotate(105deg);
    position: absolute;
    top: 20%;
    left: 23%;
    z-index: -1;
  }

  .healthcare__cards::after {
    content: '';
    width: 128px;
    height: 128px;
    background: #EC5863;
    border-radius: 30px;
    -webkit-transform: rotate(-30deg);
            transform: rotate(-30deg);
    position: absolute;
    bottom: 7%;
    right: 28%;
    z-index: -1;
  }
}

@media screen and (min-width: 953px) {
  .healthcare__cards {
    grid-template-columns: repeat(3, calc((100% - 155px) / 3));
    grid-gap: 72px;
    gap: 72px;
    padding-top: 40px;
    padding-bottom: 30px;
    margin: 0 auto;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    /* scroll-snap-type: x mandatory; */
  }


}
.card {
  padding: 17px 12px;
  background: #FFFFFF;
  box-shadow: 0px 4px 18px rgba(88, 126, 236, 0.18);
  border-radius: 8px;
  transition: all .3s ease;
}

.card:hover {
  -webkit-transform: translateY(-10px);
          transform: translateY(-10px);
}

@media screen and (min-width: 953px) {
  .card {
    padding: 37px 25px;
  }

}
.card__image {
  height: 72px;
  margin: 0;
  padding: 0;
}
.card__title {
  font-weight: 700;
  font-size: 16px;
  line-height: 1.4;
  letter-spacing: 1px;
  text-align: center;
  -webkit-font-feature-settings: 'pnum' on, 'lnum' on;
          font-feature-settings: 'pnum' on, 'lnum' on;
  margin: 38px 0 0;
}

.card__title:not(:last-child) {
  margin-bottom: 4px;
}

@media screen and (min-width: 953px) {
  .card__title {
    margin-top: 58px;
  }
}
.card__text {
  font-weight: 400;
  line-height: 1.4;
  text-align: center;
  letter-spacing: 1px;
  color: #616161;
  -webkit-font-feature-settings: 'pnum' on, 'lnum' on;
          font-feature-settings: 'pnum' on, 'lnum' on;
  padding: 0;
  margin: 0;
}
.footer {
  padding: 35px 30px;
}

@media screen and (min-width: 867.98px) {
  .footer {
    padding: 45px 50px;
  }
}
.footer__links {
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
  grid-gap: 57px;
  gap: 57px;
  align-items: center;
  justify-content: center;
}

.footer__link {
  text-decoration: none;
  transition: all .3s ease;
  height: 55px;
  width: 155px;
}

.footer__link:hover {
  opacity: .8;
}
.footer__link-thumb {
  height: 100%;
  width: 100%;
  object-fit: cover;
  object-position: center;
}
.popup {
  background: rgba(0, 0, 0, 0.5);
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  visibility: hidden;
  opacity: 0;
  transition-property: opacity, visibility;
  transition-duration: .3s;
  transition-timing-function: linear;
  z-index: 10;
}
.popup_opened {
  visibility: visible;
  opacity: 1;
  display: block;
  transition-property: opacity, visibility;
  transition-duration: .3s;
  transition-timing-function: linear;
}
.popup__overlay {
  background-color: transparent;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 9;
}
.popup__container {
  padding: 25px 22px;
  margin: 100px 35px 0;
  background-color: #fff;
  color: #000;
  border-radius: 10px;
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);

  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  position: relative;
  z-index: 10;
}


@media screen and (min-width: 619px) {
  .popup__container {
    max-width: 430px;
    margin: 110px auto 0;
    padding: 34px 36px 37px;
  }
}

@media screen and (min-width: 1025px) {
  .popup__container {
    margin-top: 150px;
  }
}
.popup__close {
  position: absolute;
  top: -45px;
  right: 0px;
}

@media screen and (min-width: 619px) {
  .popup__close {
    top: -40px;
    right: -40px;
  }
}
.popup__close-button {
  width: 40px;
  height: 40px;
  position: relative;
  display: block;
  transition: all .3s ease;
}

.popup__close-button::after {
  content: "";
  background: #000;
  height: 2px;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
  position: absolute;
  top: 50%;
  width: 100%;
  right: 0;
  background: #000;
  height: 2px;
  transform: rotate(45deg);
}

.popup__close-button::before {
  content: "";
  position: absolute;
  top: 50%;
  width: 100%;
  right: 0;
  background: #000;
  height: 2px;
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
}

.popup__close-button:hover {
  cursor: pointer;
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
  background: transparent;
}
